.home-display {
    display: flex;
    margin-top: 2cm;

    justify-content: space-between;
    align-items: flex-start;
}
.Home{
    display: flex;
    flex-direction: column;
    
    justify-content: center;
    row-gap: 2cm;
}

.home-right{
    width: 15cm;
}
.backI {
    width: 9cm;
    height: auto;
    border-radius: 100px;
}

.home-buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: .5cm;
    column-gap: .5cm;
    row-gap: .4cm;
}

.about-button {
    padding: .4cm 1cm;
    border: thin solid rgb(50, 50, 50);
    font-size: 18px;
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(65, 93, 230);
        border: thin solid #0f0f13;
    }
}


.resume-button {
    padding: .4cm 1cm;
    border: thin solid rgb(50, 50, 50);
    font-size: 18px;
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(230, 65, 87);
        border: thin solid #0f0f13;
    }
}

.home-box {
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.bio {
    position: relative;
    font-size: 25px;
    font-family: Titillium Web;
    margin-top: -1.2cm;
    align-items: center;
    line-height: .85cm;
    color: #BA8ED8;
    width: 100%;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;
    width: fit-content;
}

.projects-display-heading {
    font-size: 35px;
    text-align: center;
    color: white;
    font-family: Dancing Script;
}



.project{
    width: 7cm;
    height: auto;
    border: 1px solid white;
}